/*global document, Office*/

Office.onReady((info) => {
    if (info.host === Office.HostType.Excel) {
      // Disable the button when the add-in is loaded
      disableButton();
    }
  });

function disableButton() {
    Office.ribbon.requestUpdate({
      tabs: [
        {
          id: "ExcelGedcom.Tab", // Custom tab ID defined in the manifest
          groups: [
            {
              id: "ExcelGedcom.ImportExport", // Custom group ID defined in the manifest
              controls: [
                {
                  id: "ExportButton", // Custom button ID defined in the manifest
                  enabled: false
                }
              ]
            }
          ]
        }
      ]
    });
  }